// Colors
@color_blue: #2e2771;
@colorlink_blue: #393185;
@color_pink: #e50a62;
@color_white: #ffffff;
@bg_nav: #393185;
@bg_info: #2e2771;
@bg_form: #ecf5fd;


// Fonts 
@font-face {
    font-family: 'SegoeUI_reg';
    src: url('../fonts/SegoeUI.eot');
    src: url('../fonts/SegoeUI.eot?#iefix') format('embedded-opentype'),
        url('../fonts/SegoeUI.woff2') format('woff2'),
        url('../fonts/SegoeUI.woff') format('woff'),
        url('../fonts/SegoeUI.ttf') format('truetype'),
        url('../fonts/SegoeUI.svg#SegoeUI') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'SegoeUI_black';
    src: url('../fonts/SegoeUIBlack.eot');
    src: url('../fonts/SegoeUIBlack.eot?#iefix') format('embedded-opentype'),
        url('../fonts/SegoeUIBlack.woff2') format('woff2'),
        url('../fonts/SegoeUIBlack.woff') format('woff'),
        url('../fonts/SegoeUIBlack.ttf') format('truetype'),
        url('../fonts/SegoeUIBlack.svg#SegoeUIBlack') format('svg');
    font-weight: 900;
    font-style: normal;
}

@font-face {
    font-family: 'FuturaFuturisCondBoldC';
    src: url('../fonts/FuturaFuturisCondBoldC.eot');
    src: url('../fonts/FuturaFuturisCondBoldC.eot?#iefix') format('embedded-opentype'),
        url('../fonts/FuturaFuturisCondBoldC.woff2') format('woff2'),
        url('../fonts/FuturaFuturisCondBoldC.woff') format('woff'),
        url('../fonts/FuturaFuturisCondBoldC.ttf') format('truetype'),
        url('../fonts/FuturaFuturisCondBoldC.svg#FuturaFuturisCondBoldC') format('svg');
    font-weight: bold;
    font-style: normal;
}

// Mixins for fonts
.regSeg() {
	font-family: 'SegoeUI_reg', sans-serif;
}

.blackSeg() {
	font-family: 'SegoeUI_black', sans-serif;
}

.boldFcond() {
		font-family: 'FuturaFuturisCondBoldC', sans-serif;
}


// responsive block
.relation {
		position: relative;
		max-width: 95%;
		margin-left: 10px;
		&__ration {
					padding-top: 56.25%;
					height: 0;
		}

		&__content {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
		}

}