@import "project.less";
@import "smart-grid.less";


/* ========== Settings styles for project ============== */ 
body {
    .regSeg();
}
a {
    text-decoration: none;

    &:focus {
        outline: none!important;
    }
}
button {
    &:focus {
        outline: none!important;
    }
}

.reset();
.wrapper{
    .wrapper();
} 

/*  for title's section */
.section {
    &__header {
        margin: 0 0 40px 210px;
        h2 {
            .boldFcond();
            font-size: 24px;
            text-transform: uppercase;
            color: @color_blue;
            position: relative;
            

            &::before {
                content: "";
                display: block;
                width: 84px;
                height: 3px;
                background: @color_pink;
                position: absolute;
                left: -200px;
                bottom: 10px;
            }
        }
    }
}

.double {
    display: flex;
    justify-content: space-between;
    h3 {
        .boldFcond();
        font-size: 24px;
        text-transform: uppercase;
        color: @color_blue;
        margin-right: 125px;
    }
}

/* for all the rowsItems */ 
.rowItems {
    margin-left: 200px!important;
}

/* for buttons */ 
.btn {
    font-size: 12px;
    text-transform: uppercase;
    color: @color_white;
    padding: 12px 40px;
    width: 206px;
    border: none;
    cursor: pointer;
    transition: background .5s;

    &:hover {
        background: lighten(@color_pink, 10%);
    }

    &-pink {
        background: @color_pink;
    }
}



/* ========== Header ============== */ 
.headerTop {
    padding: 10px 0 10px;
    border-bottom: 1px solid #e2e2ed;

    a {
        font-size: 11px;
        text-transform: uppercase;
        color: @color_blue;
        &:hover {
            text-decoration: underline;
        }
    }

    .row {
        display: flex;
        justify-content: space-between;
    }

    .topmenu {
        display: flex;
        li {
            margin-right: 55px;
            &:last-child {
                margin-right: 0;
            }
        }
    }

    .boormark {
        display: flex;
        margin-right: 26px;
        &__img {
            margin-right: 15px;
            cursor: pointer;
        }
        &__link {
            margin-top: -1px;
            a {
                &:hover {
                    text-decoration: none;
                    color: @color_pink;
                }
            }
        }
    }
}
.headerBottom {
    padding: 19px 0 28px;

    a {
        font-size: 16px;
        color: @colorlink_blue;
        text-decoration: underline;
        &:hover {
            text-decoration: none;
        }
    }

    .text {
        font-size: 16px;
        color: #262626;
    }

    .topBlock {
        margin-bottom: 15px;
    }

    .logo {
        .size(2.5);
    }

    .contacts {
        position: relative;
        padding-left: 40px;
        .size(3.5);
        &__phone {
            .blackSeg();
        }
        &::before {
            content: '';
            display: block;
            width: 16px;
            height: 16px;
            background: url('../img/header/icon-phone.png') left no-repeat;
            position: absolute;
            left: 0px;
            top: 7px;
        }
    }

    .address {
        position: relative;
        padding-left: 40px;
        .size(3.8);
        &::before {
            content: '';
            display: block;
            width: 12px;
            height: 20px;
            background: url('../img/header/icon-address.png') left no-repeat;
            position: absolute;
            left: 0px;
            top: 3px;
        }
    }
    
    .meta {
        padding-top: 4px;
        .size(2.2);
    }

    .row {
        .row-flex();
        .item {
           .col();
        }
    }
}


/* ========== Navigation ============== */ 
.nav {
    max-width: 1440px;
    margin: 0 auto;
    background: @bg_nav;

    .mainMenu {
        height: 75px;
        display: flex;
        flex-wrap: wrap;

        &__link {
            position: relative;
            flex-grow: 1;
            .boldFcond();
            font-size: 24px;
            text-transform: uppercase;
            letter-spacing: 2.5px;
            &:nth-child(2) {
                text-align: center;
                flex-grow: 5;
            }
            > a {
                color: @color_white;
                display: block;
                height: 100%;
                line-height: 75px;
                padding-left: 20px;
                position: relative;
                &:hover {
                    background: darken(@bg_nav, 10%);
                }
                &::after {
                    content: "";
                    display: block;
                    width: 10px;
                    height: 6px;
                    background: url('../img/navigation/icon-arrow.png') no-repeat;
                    position: absolute;
                    right: 20px;
                    top: 35px;
                }
            }
            a.mainMenu__link-small {
                &::after {
                    right: 120px;
                }
            }
            img {
                position: relative;
                right: -18px;
                top: -5px;
                cursor: pointer;
            }
        }

        &__sub {
            display: none;
            position: absolute;
            left: 0;
            top: 75px;
            z-index: 100;
            width: 100%;
            li {
                a {
                    border-top: 1px solid #fff;
                    display: block;
                    padding: 10px 20px;
                    background: @bg_nav;
                    color: @color_white;
                    &:hover {
                        background: darken(@bg_nav, 10%);
                    }
                }
            }
        }
    }
}


/* ========== Main ============== */ 
.main {
    max-width: 1440px;
    margin: 0 auto;
    padding: 70px 0 0px;
    background: url('../img/main/bg-main.jpg') center center no-repeat;

    .mainHeader {
        padding-bottom: 55px;

        h1 {
            .boldFcond();
            font-size: 108px;
            text-transform: uppercase;
            color: @color_white;
            line-height: .9;
            position: relative;
            
            &::before {
                content: "";
                display: block;
                width: 84px;
                height: 3px;
                background: @color_pink;
                position: absolute;
                bottom: -50px;
                left: 0;
            }
        }
    }

    .mainContent {
        padding-top: 55px;
        .row {
            display: flex;
            justify-content: space-between;
        }

        &__menu {
            margin-left: -100px;
            li {
                .blackSeg();
                font-size: 14px;
                border-bottom: 1px solid #473d94;
                &:last-child {
                    border-bottom: none;
                }
                a {
                    display: block;
                    padding: 18px 30px;
                    color: @color_white;
                    background: #393185;
                    &:hover {
                        background: @color_pink;
                    }
                }
            }
        }

        &__title {
            .blackSeg();
            font-size: 30px;
            color: @color_white;
            line-height: 1.3;
            flex-basis: 300px;
        }

        .contentList {
            ul {
                li {
                    .regSeg();
                    font-size: 24px;
                    color: @color_white;
                    margin-bottom: 28px;
                    position: relative;
                    &::before {
                        content: "";
                        display: block;
                        width: 16px;
                        height: 13px;
                        background: url('../img/main/icon-tick.png') center center no-repeat;
                        position: absolute;
                        left: -35px;
                        top: 10px;
                    }

                }
            }
        }
            
        .contentOrder {
            &__phone {
                .blackSeg();
                font-size: 30px;
                color: @color_white;
                margin-bottom: 30px;
            }

            &__btn {
                width: 100%;
                padding: 21px 0;
            }
        }

    }
}


/* ========== section.Popular ============== */ 
.popular {
    padding-top: 60px;

    .popularItems {
        .row-flex();

        .popularItem {
            .col();
            .size(4);

            &__title {
                .blackSeg();
                font-size: 18px;
                a {
                    color: @color_blue;
                    text-decoration: underline;
                    &:hover {
                        text-decoration: none;
                    }
                }
            }
            &__img {
                margin: 25px 0 30px;
                transform: scale(1);
                transition: transform .5s;
                img {
                    width: 295px;
                    height: 221px;
                }
                &:hover {
                    transform: scale(1.1);
                }
            }
        }

        .personalItem {
            width: 295px;
            height: 221px;
            margin-top: 48px;
            border: 8px solid @color_pink;
            box-sizing: border-box;
            transform: scale(1);
            transition: transform .5s;
            &:hover {
                transform: scale(1.1);
                cursor: pointer;
            }

            &__link {
                padding: 35px 33px;
                a {
                    .blackSeg();
                    font-size: 18px;
                    color: @color_pink;
                    text-decoration: underline;

                    &:hover {
                        text-decoration: none;
                    }
                }
            }

            &__arrow {
                text-align: right;
                a {
                    display: inline-block;
                    margin-right: 33px;
                    transition: transform .5s;
                    &:hover {
                        transform: translateX(5px);
                    }
                }
            }
        }

    }
}


/* ========== section.Price ============== */ 
.price {
    padding: 30px 0;

    table.priceTable {
        width: 95%;
        font-size: 14px;
        color: #303030;
        margin-left: 8px;
        border: 1px solid #e6f0f8;

        thead {
            .blackSeg();
            font-size: 16px;
            color: @color_white;
            background: @colorlink_blue;
            tr {
                height: 60px;
                th {
                    text-align: center;
                    &:first-child {
                        padding-left: 30px;
                        text-align: left;
                        width: 350px;
                    }
                }
            }
        }

        tbody {
            tr {
                height: 40px;
                &:nth-child(2n-1) {
                    background: #f2f7fb;
                }
                td {
                    text-align: center;
                    &:first-child {
                        padding-left: 30px;
                        text-align: left;
                    }
                    &.activeCell {
                        .blackSeg();
                        color: @color_white;
                        background: @color_pink;
                    }
                }
            }
        }
    }

    .priceAdd {
        width: 92%;
        padding: 0 0 25px 30px;
        margin-left: 8px;
        border-bottom: 1px solid #e6f0f8;
        &__title {
            .blackSeg();
            font-size: 14px;
            color: @colorlink_blue;
            margin: 20px 0 15px;
        }
        &__text {
            p {
                line-height: 1.8;
            }
        }
    }
}


/* ========== section.Video ============== */ 
.video {
    padding: 30px 0;

    &__preview {
        background: rgba(0, 0, 0, .7);
        position: relative;
        
        .videoBlock {
            max-width: 500px;
            box-sizing: border-box;
            text-align: center;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -27%);
            &__link {
                display: inline-block;
            }
            &__play {
                .blackSeg();
                font-size: 18px;
                color: @color_white;
                margin-top: 35px;
                cursor: default;
            }
        }
    }
}


/* ========== section.Payment ============== */ 
.payment {
    padding-top: 30px;
    .paymentItems {
        .row-flex();

        .paymentItem {
            .col();
            .size(4);
            display: flex;
            align-items: center;
            margin-bottom: 25px;
            &__img {
                margin-right: 40px;
            }
            &__title {
                font-size: 16px;
                font-weight: bold;
                line-height: 1.2;
                color: @colorlink_blue;
                max-width: 150px;
            }
        }
    }
}


/* ========== section.Answer ============== */ 
.answer {
    padding-top: 30px;

    .wrapper {
        background: url('../img/answer/img-question.png') left 30px top 90px no-repeat;
    }

    .answerItems {
        padding-left: 20px;
        .answerItem {
            margin-bottom: 25px;

            &__link {
                font-size: 18px;
                color: #393277;
                text-decoration: underline;
                &:hover {
                    text-decoration: none;
                }
            }
        }

        .activeAnswer {
            background: #f5fafe;
            padding: 35px 40px;
            &__link {
                .blackSeg();
                font-size: 18px;
                color: @colorlink_blue;
                text-decoration: none;
            }
            &__list {
                display: none;
                position: relative;
                ol {
                    list-style-type: none;
                    counter-reset: item;
                    padding-left: 50px;
                    margin-top: 30px;
                    li {
                        font-size: 16px;
                        color: #303030;
                        margin-bottom: 25px;
                        position: relative;

                         &::before {
                          content: counter(item) '. ';
                          counter-increment: item;
                          .blackSeg();
                          color: @colorlink_blue;
                          background: #fff;
                          position: absolute;
                          top: 5px;
                          left: -50px;
                         }
                    }
                }

                .closeAnswer {
                    font-size: 14px;
                    color: @colorlink_blue;
                    position: absolute;
                    right: 0;
                    top: -50px;
                    margin-right: 20px;

                    &__icon {
                        margin-left: 10px;
                    }
                }


            }

            
        }
    }
}


/* ========== section.Form ============== */
.form {
    padding: 30px 0;

    .rowItems {
        padding-left: 20px;
    }

    &__title {
        width: 395px;
        padding: 15px 0;
        background: @color_white;
        box-sizing: border-box;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        margin-top: -35px;
    }

    .formWrap {
        border: 8px solid @colorlink_blue;
        position: relative;
        width: 96%;
        h2 {
            .boldFcond();
            font-size: 24px;
            color: @color_blue;
            text-transform: uppercase;
            text-align: center;
        }
    }
    .formBlock {
        background: @bg_form;
        padding: 70px;
    }

    .formInput {
        display: flex;
        input {
            font-size: 24px;
            color: @colorlink_blue;
            opacity: .7;
            border: none;
            border-bottom: 3px solid #9293c1;
            padding: 5px 0 25px;
            margin-bottom: 40px;
            margin-right: 20px;
            width: 50%;
            background: transparent;
            box-sizing: border-box;
            word-wrap: wrap;

            &:focus {
                outline: none;
            }
            
            &::-webkit-input-placeholder {
                font-size: 24px;
                color: @colorlink_blue;
                opacity: .7;
            }

            &::-moz-placeholder {
                font-size: 24px;
                color: @colorlink_blue;
                opacity: .7;
            }
        }

        textarea {
            font-size: 24px;
            color: @colorlink_blue;
            opacity: .7;
            border: none;
            border-bottom: 3px solid #9293c1;
            padding: 5px 0px;
            margin-bottom: 40px;
            background: transparent;
            min-height: 100px;
            min-width: 100%;
            word-wrap: wrap;
            &:focus {
                outline: none;
            }
            &::-webkit-input-placeholder {
                font-size: 24px;
                color: @colorlink_blue;
                opacity: .7;
            }
            &::-moz-placeholder {
                font-size: 24px;
                color: @colorlink_blue;
                opacity: .7;
            }
                
        }
    }

    .formFile {
        &__btn {
            display: none;
        }
        &__text {
            display: inline-block;
            font-size: 14px;
            color: @colorlink_blue;
            padding-left: 40px;
            position: relative;
            &::before {
                content: "";
                display: block;
                width: 20px;
                height: 20px;
                background: url('../img/form/icon-file.png') no-repeat;
                position: absolute;
                top: 0;
                left: 0;
            }
            &:hover {
                text-decoration: underline;
                cursor: pointer;
            }
        }
    }

    .formBtn {
        margin-top: 30px;
        text-align: center;
        .btn {
            width: 296px;
            padding: 20px 0;
        }
    }
}


/* ========== section.Question ============== */
.question {
    padding:30px 0;

    .section__header {
        margin-left: 200px;
    }

    .questionItems {
        .row-flex();

        .questionItem {
            .col-padding();
            .size(4);
            border: 3px solid #e6f0f8;
            padding: 40px 0;
            text-align: center;
            transition: all 1s;

            display: flex;
            flex-direction: column;
            justify-content: space-between;

            &:hover {
                cursor: pointer;
                border-color: @color_pink;
                .questionItem__action a {
                    color: @color_pink;
                }
            }

            .callQuestion {
                font-size: 24px;
                margin-bottom: 30px;
                &__title {
                    color: #3b3b3b;
                }
                &__subTitle {
                    .blackSeg();
                    color: @colorlink_blue;
                }
            }

            &__img {
                margin-bottom: 20px;
            } 

            &__action {
                font-size: 16px;
                span {
                    display: block;
                    color: #9ea3c4;
                    margin-bottom: 3px;
                    text-decoration: underline;
                }
                a {
                    .blackSeg();
                    color: @colorlink_blue;
                    text-decoration: underline;

                    &:hover {
                        text-decoration: none;
                    }
                }
            }
        }
    }
}


/* ========== section.Portfolio ============== */
.portfolio {
    padding: 30px 0;

    .section__header {
        margin-left: 200px;
    }

    .rowItems {
        margin-left: 180px!important;
    }

    .portfolioSlider {

        &__item {
            width: 320px!important;
            transform: scale(1);
            transition: transform 1s;
            &:hover {
                transform: scaleY(1.2);
            }
            img {
                max-width: 100%;
            }
        }
    }
}


/* ========== section.Advantages ============== */
.advantages {
    padding: 30px 0; 

    .advantagesBlockItem {
        display: flex;
        justify-content: space-between;
        margin-bottom: 40px;
        &:last-child {
            margin-bottom: 10px;
        }
        .advantagesBlockItemBar {
            width: 31%;
            box-sizing: border-box;
            border-bottom: 3px solid @color_pink;
            align-self: flex-start;
            &__title {
                .blackSeg();
                max-width: 230px;
                font-size: 18px;
                color: #2e2771;          
                padding-bottom: 30px;
            }
        }
        .advantagesBlockItemContent {
            width: 69%;
            margin-left: 30px;
            padding-right: 20px;
            box-sizing: border-box;
            font-size: 16px;
            color: #303030;
            &__text {
                margin-bottom: 20px;
            }
            &__title {
                .blackSeg();
                color: #2e2771;
                margin-bottom: 20px;
            }
            &__list {
                margin-bottom: 20px;

                li {
                    margin-bottom: 20px;
                    &::before {
                        content: '—';
                        margin-right: 7px;
                    }
                }
            }
        }
    }
}


/* ========== Footer ============== */
.footer {
    max-width: 1440px;
    margin: 0 auto;
}

/* ========== div.Info ============== */
.info {
    padding-top: 80px;
    background: @bg_info;
    position: relative;
    z-index: 2000;

    .infoItems {
        .row-flex();
        
        .infoItem {
            .col();
            font-size: 14px;
            color: @color_white;
            margin-bottom: 60px;

            &:last-child {
                margin-bottom: -2px;
            }

            &__left {
                .size(3);
            }
            &__center {
                .size(6);
            }
            &__right {
                .size(3);
            }

            &__title {
                .boldFcond();
                font-size: 16px;
                color: @color_white;
                text-transform: uppercase;
                letter-spacing: 1px;
                padding-bottom: 20px;
                margin-bottom: 30px;
                position: relative;
                &:last-child {
                    margin-bottom: 0;
                }
                &::before {
                    content: "";
                    display: block;
                    width: 60px;
                    height: 2px;
                    background: @color_pink;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                }
            }

            .infoItemListWrapper {
                display: flex;

                .infoItem__list {
                    margin-right: 60px;
                }
            }

            &__list {
                li {
                    margin-bottom: 10px;
                    a {
                        color: @color_white;
                        &:hover {
                            text-decoration: underline;
                        }
                    }
                }
            }

            &__text {
                margin-bottom: 35px;
            }

            .contacts {
                position: relative;
                padding-left: 40px;
                margin-bottom: 30px;

                .topBlock {
                    margin-bottom: 10px;
                }
                &__phone {
                    .blackSeg();
                }
                &__email {
                    a {
                        color: #b1c2e8;
                    }
                }
                &::before {
                    content: '';
                    display: block;
                    width: 16px;
                    height: 16px;
                    background: url('../img/info/icon-phone-white.png') left no-repeat;
                    position: absolute;
                    left: 0px;
                    top: 7px;
                }
                a {
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }

            .address {
                position: relative;
                padding-left: 40px;
                margin-bottom: 30px;
                .topBlock {
                    margin-bottom: 10px;
                }
                &__find {
                    a {
                        color: #b1c2e8;
                    }
                }
                &::before {
                    content: '';
                    display: block;
                    width: 12px;
                    height: 20px;
                    background: url('../img/info/icon-map-white.png') left no-repeat;
                    position: absolute;
                    left: 0px;
                    top: 3px;
                }
                a {
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    }
}

/* ========== div.Map ============== */
.map {
    max-width: 1440px;
    margin: 0 auto;
    background: #ccc;
    &__block {
        height: 500px;
    }
}




