.relation {
  position: relative;
  max-width: 95%;
  margin-left: 10px;
}

.relation__ration {
  padding-top: 56.25%;
  height: 0;
}

.relation__content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
