/* ========== Settings styles for project ============== */

body {
  font-family: 'SegoeUI_reg', sans-serif;
}

a {
  text-decoration: none;
}

a:focus {
  outline: none!important;
}

button:focus {
  outline: none!important;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1240px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

/*  for title's section */

.section__header {
  margin: 0 0 40px 210px;
}

.section__header h2 {
  font-family: 'FuturaFuturisCondBoldC', sans-serif;
  font-size: 24px;
  text-transform: uppercase;
  color: #2e2771;
  position: relative;
}

.section__header h2::before {
  content: "";
  display: block;
  width: 84px;
  height: 3px;
  background: #e50a62;
  position: absolute;
  left: -200px;
  bottom: 10px;
}

.double {
  display: flex;
  justify-content: space-between;
}

.double h3 {
  font-family: 'FuturaFuturisCondBoldC', sans-serif;
  font-size: 24px;
  text-transform: uppercase;
  color: #2e2771;
  margin-right: 125px;
}

/* for all the rowsItems */

.rowItems {
  margin-left: 200px!important;
}

/* for buttons */

.btn {
  font-size: 12px;
  text-transform: uppercase;
  color: #ffffff;
  padding: 12px 40px;
  width: 206px;
  border: none;
  cursor: pointer;
  transition: background .5s;
}

.btn:hover {
  background: #f62c7d;
}

.btn-pink {
  background: #e50a62;
}

/* ========== Header ============== */

.headerTop {
  padding: 10px 0 10px;
  border-bottom: 1px solid #e2e2ed;
}

.headerTop a {
  font-size: 11px;
  text-transform: uppercase;
  color: #2e2771;
}

.headerTop a:hover {
  text-decoration: underline;
}

.headerTop .row {
  display: flex;
  justify-content: space-between;
}

.headerTop .topmenu {
  display: flex;
}

.headerTop .topmenu li {
  margin-right: 55px;
}

.headerTop .topmenu li:last-child {
  margin-right: 0;
}

.headerTop .boormark {
  display: flex;
  margin-right: 26px;
}

.headerTop .boormark__img {
  margin-right: 15px;
  cursor: pointer;
}

.headerTop .boormark__link {
  margin-top: -1px;
}

.headerTop .boormark__link a:hover {
  text-decoration: none;
  color: #e50a62;
}

.headerBottom {
  padding: 19px 0 28px;
}

.headerBottom a {
  font-size: 16px;
  color: #393185;
  text-decoration: underline;
}

.headerBottom a:hover {
  text-decoration: none;
}

.headerBottom .text {
  font-size: 16px;
  color: #262626;
}

.headerBottom .topBlock {
  margin-bottom: 15px;
}

.headerBottom .logo {
  width: calc(20.833333333333336% - 20px);
}

.headerBottom .contacts {
  position: relative;
  padding-left: 40px;
  width: calc(29.166666666666668% - 20px);
}

.headerBottom .contacts__phone {
  font-family: 'SegoeUI_black', sans-serif;
}

.headerBottom .contacts::before {
  content: '';
  display: block;
  width: 16px;
  height: 16px;
  background: url('../img/header/icon-phone.png') left no-repeat;
  position: absolute;
  left: 0px;
  top: 7px;
}

.headerBottom .address {
  position: relative;
  padding-left: 40px;
  width: calc(31.666666666666668% - 20px);
}

.headerBottom .address::before {
  content: '';
  display: block;
  width: 12px;
  height: 20px;
  background: url('../img/header/icon-address.png') left no-repeat;
  position: absolute;
  left: 0px;
  top: 3px;
}

.headerBottom .meta {
  padding-top: 4px;
  width: calc(18.333333333333336% - 20px);
}

.headerBottom .row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.headerBottom .row .item {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
}

/* ========== Navigation ============== */

.nav {
  max-width: 1440px;
  margin: 0 auto;
  background: #393185;
}

.nav .mainMenu {
  height: 75px;
  display: flex;
  flex-wrap: wrap;
}

.nav .mainMenu__link {
  position: relative;
  flex-grow: 1;
  font-family: 'FuturaFuturisCondBoldC', sans-serif;
  font-size: 24px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
}

.nav .mainMenu__link:nth-child(2) {
  text-align: center;
  flex-grow: 5;
}

.nav .mainMenu__link > a {
  color: #ffffff;
  display: block;
  height: 100%;
  line-height: 75px;
  padding-left: 20px;
  position: relative;
}

.nav .mainMenu__link > a:hover {
  background: #292360;
}

.nav .mainMenu__link > a::after {
  content: "";
  display: block;
  width: 10px;
  height: 6px;
  background: url('../img/navigation/icon-arrow.png') no-repeat;
  position: absolute;
  right: 20px;
  top: 35px;
}

.nav .mainMenu__link a.mainMenu__link-small::after {
  right: 120px;
}

.nav .mainMenu__link img {
  position: relative;
  right: -18px;
  top: -5px;
  cursor: pointer;
}

.nav .mainMenu__sub {
  display: none;
  position: absolute;
  left: 0;
  top: 75px;
  z-index: 100;
  width: 100%;
}

.nav .mainMenu__sub li a {
  border-top: 1px solid #fff;
  display: block;
  padding: 10px 20px;
  background: #393185;
  color: #ffffff;
}

.nav .mainMenu__sub li a:hover {
  background: #292360;
}

/* ========== Main ============== */

.main {
  max-width: 1440px;
  margin: 0 auto;
  padding: 70px 0 0px;
  background: url('../img/main/bg-main.jpg') center center no-repeat;
}

.main .mainHeader {
  padding-bottom: 55px;
}

.main .mainHeader h1 {
  font-family: 'FuturaFuturisCondBoldC', sans-serif;
  font-size: 108px;
  text-transform: uppercase;
  color: #ffffff;
  line-height: .9;
  position: relative;
}

.main .mainHeader h1::before {
  content: "";
  display: block;
  width: 84px;
  height: 3px;
  background: #e50a62;
  position: absolute;
  bottom: -50px;
  left: 0;
}

.main .mainContent {
  padding-top: 55px;
}

.main .mainContent .row {
  display: flex;
  justify-content: space-between;
}

.main .mainContent__menu {
  margin-left: -100px;
}

.main .mainContent__menu li {
  font-family: 'SegoeUI_black', sans-serif;
  font-size: 14px;
  border-bottom: 1px solid #473d94;
}

.main .mainContent__menu li:last-child {
  border-bottom: none;
}

.main .mainContent__menu li a {
  display: block;
  padding: 18px 30px;
  color: #ffffff;
  background: #393185;
}

.main .mainContent__menu li a:hover {
  background: #e50a62;
}

.main .mainContent__title {
  font-family: 'SegoeUI_black', sans-serif;
  font-size: 30px;
  color: #ffffff;
  line-height: 1.3;
  flex-basis: 300px;
}

.main .mainContent .contentList ul li {
  font-family: 'SegoeUI_reg', sans-serif;
  font-size: 24px;
  color: #ffffff;
  margin-bottom: 28px;
  position: relative;
}

.main .mainContent .contentList ul li::before {
  content: "";
  display: block;
  width: 16px;
  height: 13px;
  background: url('../img/main/icon-tick.png') center center no-repeat;
  position: absolute;
  left: -35px;
  top: 10px;
}

.main .mainContent .contentOrder__phone {
  font-family: 'SegoeUI_black', sans-serif;
  font-size: 30px;
  color: #ffffff;
  margin-bottom: 30px;
}

.main .mainContent .contentOrder__btn {
  width: 100%;
  padding: 21px 0;
}

/* ========== section.Popular ============== */

.popular {
  padding-top: 60px;
}

.popular .popularItems {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.popular .popularItems .popularItem {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 20px);
}

.popular .popularItems .popularItem__title {
  font-family: 'SegoeUI_black', sans-serif;
  font-size: 18px;
}

.popular .popularItems .popularItem__title a {
  color: #2e2771;
  text-decoration: underline;
}

.popular .popularItems .popularItem__title a:hover {
  text-decoration: none;
}

.popular .popularItems .popularItem__img {
  margin: 25px 0 30px;
  transform: scale(1);
  transition: transform .5s;
}

.popular .popularItems .popularItem__img img {
  width: 295px;
  height: 221px;
}

.popular .popularItems .popularItem__img:hover {
  transform: scale(1.1);
}

.popular .popularItems .personalItem {
  width: 295px;
  height: 221px;
  margin-top: 48px;
  border: 8px solid #e50a62;
  box-sizing: border-box;
  transform: scale(1);
  transition: transform .5s;
}

.popular .popularItems .personalItem:hover {
  transform: scale(1.1);
  cursor: pointer;
}

.popular .popularItems .personalItem__link {
  padding: 35px 33px;
}

.popular .popularItems .personalItem__link a {
  font-family: 'SegoeUI_black', sans-serif;
  font-size: 18px;
  color: #e50a62;
  text-decoration: underline;
}

.popular .popularItems .personalItem__link a:hover {
  text-decoration: none;
}

.popular .popularItems .personalItem__arrow {
  text-align: right;
}

.popular .popularItems .personalItem__arrow a {
  display: inline-block;
  margin-right: 33px;
  transition: transform .5s;
}

.popular .popularItems .personalItem__arrow a:hover {
  transform: translateX(5px);
}

/* ========== section.Price ============== */

.price {
  padding: 30px 0;
}

.price table.priceTable {
  width: 95%;
  font-size: 14px;
  color: #303030;
  margin-left: 8px;
  border: 1px solid #e6f0f8;
}

.price table.priceTable thead {
  font-family: 'SegoeUI_black', sans-serif;
  font-size: 16px;
  color: #ffffff;
  background: #393185;
}

.price table.priceTable thead tr {
  height: 60px;
}

.price table.priceTable thead tr th {
  text-align: center;
}

.price table.priceTable thead tr th:first-child {
  padding-left: 30px;
  text-align: left;
  width: 350px;
}

.price table.priceTable tbody tr {
  height: 40px;
}

.price table.priceTable tbody tr:nth-child(2n-1) {
  background: #f2f7fb;
}

.price table.priceTable tbody tr td {
  text-align: center;
}

.price table.priceTable tbody tr td:first-child {
  padding-left: 30px;
  text-align: left;
}

.price table.priceTable tbody tr td.activeCell {
  font-family: 'SegoeUI_black', sans-serif;
  color: #ffffff;
  background: #e50a62;
}

.price .priceAdd {
  width: 92%;
  padding: 0 0 25px 30px;
  margin-left: 8px;
  border-bottom: 1px solid #e6f0f8;
}

.price .priceAdd__title {
  font-family: 'SegoeUI_black', sans-serif;
  font-size: 14px;
  color: #393185;
  margin: 20px 0 15px;
}

.price .priceAdd__text p {
  line-height: 1.8;
}

/* ========== section.Video ============== */

.video {
  padding: 30px 0;
}

.video__preview {
  background: rgba(0, 0, 0, 0.7);
  position: relative;
}

.video__preview .videoBlock {
  max-width: 500px;
  box-sizing: border-box;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -27%);
}

.video__preview .videoBlock__link {
  display: inline-block;
}

.video__preview .videoBlock__play {
  font-family: 'SegoeUI_black', sans-serif;
  font-size: 18px;
  color: #ffffff;
  margin-top: 35px;
  cursor: default;
}

/* ========== section.Payment ============== */

.payment {
  padding-top: 30px;
}

.payment .paymentItems {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.payment .paymentItems .paymentItem {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 20px);
  display: flex;
  align-items: center;
  margin-bottom: 25px;
}

.payment .paymentItems .paymentItem__img {
  margin-right: 40px;
}

.payment .paymentItems .paymentItem__title {
  font-size: 16px;
  font-weight: bold;
  line-height: 1.2;
  color: #393185;
  max-width: 150px;
}

/* ========== section.Answer ============== */

.answer {
  padding-top: 30px;
}

.answer .wrapper {
  background: url('../img/answer/img-question.png') left 30px top 90px no-repeat;
}

.answer .answerItems {
  padding-left: 20px;
}

.answer .answerItems .answerItem {
  margin-bottom: 25px;
}

.answer .answerItems .answerItem__link {
  font-size: 18px;
  color: #393277;
  text-decoration: underline;
}

.answer .answerItems .answerItem__link:hover {
  text-decoration: none;
}

.answer .answerItems .activeAnswer {
  background: #f5fafe;
  padding: 35px 40px;
}

.answer .answerItems .activeAnswer__link {
  font-family: 'SegoeUI_black', sans-serif;
  font-size: 18px;
  color: #393185;
  text-decoration: none;
}

.answer .answerItems .activeAnswer__list {
  display: none;
  position: relative;
}

.answer .answerItems .activeAnswer__list ol {
  list-style-type: none;
  counter-reset: item;
  padding-left: 50px;
  margin-top: 30px;
}

.answer .answerItems .activeAnswer__list ol li {
  font-size: 16px;
  color: #303030;
  margin-bottom: 25px;
  position: relative;
}

.answer .answerItems .activeAnswer__list ol li::before {
  content: counter(item) '. ';
  counter-increment: item;
  font-family: 'SegoeUI_black', sans-serif;
  color: #393185;
  background: #fff;
  position: absolute;
  top: 5px;
  left: -50px;
}

.answer .answerItems .activeAnswer__list .closeAnswer {
  font-size: 14px;
  color: #393185;
  position: absolute;
  right: 0;
  top: -50px;
  margin-right: 20px;
}

.answer .answerItems .activeAnswer__list .closeAnswer__icon {
  margin-left: 10px;
}

/* ========== section.Form ============== */

.form {
  padding: 30px 0;
}

.form .rowItems {
  padding-left: 20px;
}

.form__title {
  width: 395px;
  padding: 15px 0;
  background: #ffffff;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  margin-top: -35px;
}

.form .formWrap {
  border: 8px solid #393185;
  position: relative;
  width: 96%;
}

.form .formWrap h2 {
  font-family: 'FuturaFuturisCondBoldC', sans-serif;
  font-size: 24px;
  color: #2e2771;
  text-transform: uppercase;
  text-align: center;
}

.form .formBlock {
  background: #ecf5fd;
  padding: 70px;
}

.form .formInput {
  display: flex;
}

.form .formInput input {
  font-size: 24px;
  color: #393185;
  opacity: .7;
  border: none;
  border-bottom: 3px solid #9293c1;
  padding: 5px 0 25px;
  margin-bottom: 40px;
  margin-right: 20px;
  width: 50%;
  background: transparent;
  box-sizing: border-box;
  word-wrap: wrap;
}

.form .formInput input:focus {
  outline: none;
}

.form .formInput input::-webkit-input-placeholder {
  font-size: 24px;
  color: #393185;
  opacity: .7;
}

.form .formInput input::-moz-placeholder {
  font-size: 24px;
  color: #393185;
  opacity: .7;
}

.form .formInput textarea {
  font-size: 24px;
  color: #393185;
  opacity: .7;
  border: none;
  border-bottom: 3px solid #9293c1;
  padding: 5px 0px;
  margin-bottom: 40px;
  background: transparent;
  min-height: 100px;
  min-width: 100%;
  word-wrap: wrap;
}

.form .formInput textarea:focus {
  outline: none;
}

.form .formInput textarea::-webkit-input-placeholder {
  font-size: 24px;
  color: #393185;
  opacity: .7;
}

.form .formInput textarea::-moz-placeholder {
  font-size: 24px;
  color: #393185;
  opacity: .7;
}

.form .formFile__btn {
  display: none;
}

.form .formFile__text {
  display: inline-block;
  font-size: 14px;
  color: #393185;
  padding-left: 40px;
  position: relative;
}

.form .formFile__text::before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background: url('../img/form/icon-file.png') no-repeat;
  position: absolute;
  top: 0;
  left: 0;
}

.form .formFile__text:hover {
  text-decoration: underline;
  cursor: pointer;
}

.form .formBtn {
  margin-top: 30px;
  text-align: center;
}

.form .formBtn .btn {
  width: 296px;
  padding: 20px 0;
}

/* ========== section.Question ============== */

.question {
  padding: 30px 0;
}

.question .section__header {
  margin-left: 200px;
}

.question .questionItems {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.question .questionItems .questionItem {
  padding-left: 10px;
  padding-right: 10px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 20px);
  border: 3px solid #e6f0f8;
  padding: 40px 0;
  text-align: center;
  transition: all 1s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.question .questionItems .questionItem:hover {
  cursor: pointer;
  border-color: #e50a62;
}

.question .questionItems .questionItem:hover .questionItem__action a {
  color: #e50a62;
}

.question .questionItems .questionItem .callQuestion {
  font-size: 24px;
  margin-bottom: 30px;
}

.question .questionItems .questionItem .callQuestion__title {
  color: #3b3b3b;
}

.question .questionItems .questionItem .callQuestion__subTitle {
  font-family: 'SegoeUI_black', sans-serif;
  color: #393185;
}

.question .questionItems .questionItem__img {
  margin-bottom: 20px;
}

.question .questionItems .questionItem__action {
  font-size: 16px;
}

.question .questionItems .questionItem__action span {
  display: block;
  color: #9ea3c4;
  margin-bottom: 3px;
  text-decoration: underline;
}

.question .questionItems .questionItem__action a {
  font-family: 'SegoeUI_black', sans-serif;
  color: #393185;
  text-decoration: underline;
}

.question .questionItems .questionItem__action a:hover {
  text-decoration: none;
}

/* ========== section.Portfolio ============== */

.portfolio {
  padding: 30px 0;
}

.portfolio .section__header {
  margin-left: 200px;
}

.portfolio .rowItems {
  margin-left: 180px!important;
}

.portfolio .portfolioSlider__item {
  width: 320px!important;
  transform: scale(1);
  transition: transform 1s;
}

.portfolio .portfolioSlider__item:hover {
  transform: scaleY(1.2);
}

.portfolio .portfolioSlider__item img {
  max-width: 100%;
}

/* ========== section.Advantages ============== */

.advantages {
  padding: 30px 0;
}

.advantages .advantagesBlockItem {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
}

.advantages .advantagesBlockItem:last-child {
  margin-bottom: 10px;
}

.advantages .advantagesBlockItem .advantagesBlockItemBar {
  width: 31%;
  box-sizing: border-box;
  border-bottom: 3px solid #e50a62;
  align-self: flex-start;
}

.advantages .advantagesBlockItem .advantagesBlockItemBar__title {
  font-family: 'SegoeUI_black', sans-serif;
  max-width: 230px;
  font-size: 18px;
  color: #2e2771;
  padding-bottom: 30px;
}

.advantages .advantagesBlockItem .advantagesBlockItemContent {
  width: 69%;
  margin-left: 30px;
  padding-right: 20px;
  box-sizing: border-box;
  font-size: 16px;
  color: #303030;
}

.advantages .advantagesBlockItem .advantagesBlockItemContent__text {
  margin-bottom: 20px;
}

.advantages .advantagesBlockItem .advantagesBlockItemContent__title {
  font-family: 'SegoeUI_black', sans-serif;
  color: #2e2771;
  margin-bottom: 20px;
}

.advantages .advantagesBlockItem .advantagesBlockItemContent__list {
  margin-bottom: 20px;
}

.advantages .advantagesBlockItem .advantagesBlockItemContent__list li {
  margin-bottom: 20px;
}

.advantages .advantagesBlockItem .advantagesBlockItemContent__list li::before {
  content: '—';
  margin-right: 7px;
}

/* ========== Footer ============== */

.footer {
  max-width: 1440px;
  margin: 0 auto;
}

/* ========== div.Info ============== */

.info {
  padding-top: 80px;
  background: #2e2771;
  position: relative;
  z-index: 2000;
}

.info .infoItems {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.info .infoItems .infoItem {
  box-sizing: border-box;
  margin-left: 10px;
  margin-right: 10px;
  word-wrap: break-word;
  font-size: 14px;
  color: #ffffff;
  margin-bottom: 60px;
}

.info .infoItems .infoItem:last-child {
  margin-bottom: -2px;
}

.info .infoItems .infoItem__left {
  width: calc(25% - 20px);
}

.info .infoItems .infoItem__center {
  width: calc(50% - 20px);
}

.info .infoItems .infoItem__right {
  width: calc(25% - 20px);
}

.info .infoItems .infoItem__title {
  font-family: 'FuturaFuturisCondBoldC', sans-serif;
  font-size: 16px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding-bottom: 20px;
  margin-bottom: 30px;
  position: relative;
}

.info .infoItems .infoItem__title:last-child {
  margin-bottom: 0;
}

.info .infoItems .infoItem__title::before {
  content: "";
  display: block;
  width: 60px;
  height: 2px;
  background: #e50a62;
  position: absolute;
  bottom: 0;
  left: 0;
}

.info .infoItems .infoItem .infoItemListWrapper {
  display: flex;
}

.info .infoItems .infoItem .infoItemListWrapper .infoItem__list {
  margin-right: 60px;
}

.info .infoItems .infoItem__list li {
  margin-bottom: 10px;
}

.info .infoItems .infoItem__list li a {
  color: #ffffff;
}

.info .infoItems .infoItem__list li a:hover {
  text-decoration: underline;
}

.info .infoItems .infoItem__text {
  margin-bottom: 35px;
}

.info .infoItems .infoItem .contacts {
  position: relative;
  padding-left: 40px;
  margin-bottom: 30px;
}

.info .infoItems .infoItem .contacts .topBlock {
  margin-bottom: 10px;
}

.info .infoItems .infoItem .contacts__phone {
  font-family: 'SegoeUI_black', sans-serif;
}

.info .infoItems .infoItem .contacts__email a {
  color: #b1c2e8;
}

.info .infoItems .infoItem .contacts::before {
  content: '';
  display: block;
  width: 16px;
  height: 16px;
  background: url('../img/info/icon-phone-white.png') left no-repeat;
  position: absolute;
  left: 0px;
  top: 7px;
}

.info .infoItems .infoItem .contacts a:hover {
  text-decoration: underline;
}

.info .infoItems .infoItem .address {
  position: relative;
  padding-left: 40px;
  margin-bottom: 30px;
}

.info .infoItems .infoItem .address .topBlock {
  margin-bottom: 10px;
}

.info .infoItems .infoItem .address__find a {
  color: #b1c2e8;
}

.info .infoItems .infoItem .address::before {
  content: '';
  display: block;
  width: 12px;
  height: 20px;
  background: url('../img/info/icon-map-white.png') left no-repeat;
  position: absolute;
  left: 0px;
  top: 3px;
}

.info .infoItems .infoItem .address a:hover {
  text-decoration: underline;
}

/* ========== div.Map ============== */

.map {
  max-width: 1440px;
  margin: 0 auto;
  background: #ccc;
}

.map__block {
  height: 500px;
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}